/**
 * 1 / 3   2
 * 0.33
 * @param numerator 
 * @param denominator 
 * @param precision 
 * @returns
 */
const toFloatPrecision = (numerator, denominator, precision ) => {
  return (
    Math.floor((numerator * 10 ** precision) / denominator) / 10 ** precision
  );
};

export { toFloatPrecision };
